.mv-loading {
	.loading-watermark {
		display: none;
	}

	&.loading {
		text-align: center;   	
	
		&:after {
			content: "\f021";
			display: inline-block;
		    font: normal normal normal 14px/1 FontAwesome;
		    font-size: inherit;
		    text-rendering: auto;
		    -webkit-font-smoothing: antialiased;
		    -webkit-animation:spin 1s linear infinite;
	    	-moz-animation:spin 1s linear infinite;
	    	animation:spin 1s linear infinite;
	    	opacity: .3;
		}

		&.page-loading {
			font-size: 5em;

			.loading-watermark {
				display: block;
			    position: relative;
			    top: 240px;
			    max-width: 100px;
			    opacity: .3;
			    margin: 0 auto;
			}

			&:after {
				margin-top: 100px;
			}
		}
	}
}

.mv-loading-icon {
	-webkit-animation:spin 1s linear infinite;
	-moz-animation:spin 1s linear infinite;
	animation:spin 1s linear infinite;
	opacity: .3;
}

.save-btn {
	position: relative;

	&.saving {
		opacity: .6;
		
		&:after {
			content: "\f021";
			position: absolute;
			top: 15px;
			right: 15px;
		    font: normal normal normal 14px/1 FontAwesome;
		    font-size: inherit;
		    text-rendering: auto;
		    -webkit-font-smoothing: antialiased;
		    -webkit-animation:spin 1s linear infinite;
	    	-moz-animation:spin 1s linear infinite;
	    	animation:spin 1s linear infinite;
	    	opacity: .3;
		}
	}
}

@-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }